<template>
  <f7-page>
    <f7-navbar title>
      <f7-nav-left>
        <f7-link popup-close text="" @click="closePopup"></f7-link>
      </f7-nav-left>

      <f7-nav-title title=""></f7-nav-title>

      <f7-nav-right>
        <f7-link text="" @click="sendComment"></f7-link>
      </f7-nav-right>
    </f7-navbar>

    <editor :editorText="editorText" placeholder="..." @text:input="textInputChange"></editor>
  </f7-page>
</template>

<script>
import Editor from '../components/editor.vue'
import { mapMutations, mapState } from 'vuex'

export default {
  data() {
    return {
      text: ''
    }
  },
  computed: {
    ...mapState({
      editorText: state => state.popup.editorText
    })
  },
  components: {
    Editor
  },
  methods: {
    ...mapMutations(['updatePopup', 'updateEditorText']),
    closePopup() {
      this.updateEditorText({ text: '' })
    },
    textInputChange(text) {
      this.updateEditorText({ text })
    },
    sendComment() {
      //  TODO: 
      this.$f7.preloader.show('...')
      setTimeout(() => {
        this.$f7.preloader.hide()
        this.updatePopup({
          key: 'commentOpened',
          value: false
        })
        this.updateEditorText({ text: '' })
      }, 1000)
    }
  }
}
</script>

